<template>
    <div style="position:relative;">
        <div style="position:absolute;left:10px;top:10px;width:80px;height:50px;z-index:5;">
            <Image src="https://www.qiyipic.com/common/fix/h5-v3/iqiyi-logo.png" />
        </div>
        <div @touchstart="search_click" style="position:absolute;right:10px;top:10px;height:30px;line-height:30px;color:#fff;z-index:5;">搜索</div>
    </div>
</template>
<script>
export default {
    methods:{
        search_click:function(){
            this.$emit('search_show_or_hide');
        },
        request_data:function(){
            this.$emit('request_data');
        }
    }
}
</script>



