<template>
    <div style="padding:10px 80px 10px 10px;position:relative;border-bottom:1px solid #e7e7e7;">
        <input placeholder="我的前半生" style="width:100%;height:30px;" @change="searchOnChange" />
        <div @click="search_hide" style="position:absolute;right:10px;top:10px;width:70px;height:30px;line-height:30px;text-align:center;">取消</div>
    </div>
</template>
<script>

export default {
    methods:{
        search_hide:function(){
            this.$emit('search_show_or_hide');
        },
        searchOnChange:function(params){
            console.log('search on change ', params);
        }
    }
}
</script>



