<template>
    <Swiper style="width:100%;height:170px;position:absolute;top:200px;left:0;z-index:1;" @swiper_end="swiper_end">
    <SwiperItem v-for="(item,index) in data.focus" :key="index" style="padding:20px;" >
        <div style="width:100%;height:130px;position:relative;background:white;border-radius:6px;padding:5px 5px 5px 90px;overflow:hidden;">
            <img :src="item.imageUrl" style="position:absolute;top:5px;left:5px;width:80px;height:120px;" />
            <span style="display:block;">{{item.focus}}</span>
            <span style="display:block;">{{item.updateStrategy}}</span>
            <span style="display:block;">主持人：{{item.hosts}}</span>
            <span style="display:block;">{{item.mainTitle}}</span>
        </div>
        </SwiperItem>
    </Swiper>
</template>
<script>
export default {
    props:['data'],
    updated:function(){
        console.log('swiperImg updated');
    },
    methods:{
        "swiper_end":function(index){
            console.log('swiper touchend ' + index)
            this.$emit('swiper_touchend',index);
        }
    },
}
</script>



