<template>
    <View>

        <Text style="display:block;height:50px;line-height:50px;fonr-size:15px;font-weight:600;padding-left:10px;">{{listTitle}}</Text>

        <channel_cell v-for="item in listData" :cellData="item" :is_channel_new_title="is_channel_new_title"/>

    </View>
</template>
<script>
import channel_cell from './channel_cell.vue';

export default {
    props:['listTitle','listData','channel_new_title'],
    components:{
        channel_cell:channel_cell,
    },
    computed:{
        is_channel_new_title:function(){
            return this.channel_new_title.hasOwnProperty(this.listTitle);
        }
    }
}
</script>



