<template>
    <View style="padding-top:50%;display:inline-block;position:relative;" :style="style">
        <Image :src="cellData.imageUrl" style="position:absolute;left:0;top:0;right:0;bottom:0;max-width:100%;max-height:100%;" />
        <Text style="display: block; padding: 0 10px; height: 30px; line-height: 30px;overflow:hidden;">{{cellData.mainTitle}}</Text>
        <Text style="position:absolute;right:0;top:0;background:red;color:white;font-size:12px;">{{tag}}</Text>
    </View>
</template>
<script>
export default {
    props:['cellData','is_channel_new_title'],
    computed:{
        style:function(){
            return this.is_channel_new_title ? {width:'50%',paddingTop:'21%'} : {width:'33.3%',paddingTop:'50%'}
        },
        tag:function(){
            return this.cellData.isExclusive ? '独家' : '';
        }
    }
}
</script>




